import { request } from 'utils';

const prefix = '/api/students';

export const getCandidates = ({
  code,
  fullname,
  gender,
  studyFieldId,
  sumGrade,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: `/Candidate`,
    method: 'GET',
    params: {
      code,
      fullname,
      gender,
      studyFieldId,
      sumGrade,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createCandidate = ({
  code,
  esId,
  firstname,
  lastname,
  dob,
  pobId,
  gender,
  schoolId,
  areaId,
  graduatedYear,
  studyField,
  testGroup,
  gradeM1,
  gradeM2,
  gradeM3,
  certTypeId,
  trainTypeId,
  permanentAddress,
} = {}) =>
  request({
    prefix,
    url: `/Candidate`,
    method: 'POST',
    params: {},
    data: {
      code,
      esId,
      firstname,
      lastname,
      dob,
      pobId,
      gender,
      schoolId,
      areaId,
      graduatedYear,
      studyField,
      testGroup,
      gradeM1,
      gradeM2,
      gradeM3,
      certTypeId,
      trainTypeId,
      permanentAddress,
    },
  });

export const getCandidateById = ({ id }) =>
  request({
    prefix,
    url: `/Candidate/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updateCandidate = ({
  id,
  code,
  esId,
  firstname,
  lastname,
  dob,
  pobId,
  gender,
  schoolId,
  areaId,
  graduatedYear,
  studyField,
  testGroup,
  gradeM1,
  gradeM2,
  gradeM3,
  certTypeId,
  trainTypeId,
  permanentAddress,
} = {}) =>
  request({
    prefix,
    url: `/Candidate/${id}`,
    method: 'PUT',
    params: {},
    data: {
      id,
      code,
      esId,
      firstname,
      lastname,
      dob,
      pobId,
      gender,
      schoolId,
      areaId,
      graduatedYear,
      studyField,
      testGroup,
      gradeM1,
      gradeM2,
      gradeM3,
      certTypeId,
      trainTypeId,
      permanentAddress,
    },
  });

export const deleteCandidate = ({ id }) =>
  request({
    prefix,
    url: `/Candidate/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });

export const createCandidateMassSaveCandidate = ({ candidates }) =>
  request({
    prefix,
    url: `/Candidate/MassSaveCandidate`,
    method: 'POST',
    params: {},
    data: { candidates },
  });

export const uploadFilecreateCandidate = ({ trainTypeId }) =>
  request({
    prefix,
    url: `/Candidate/${trainTypeId}/UploadFile`,
    method: 'POST',
    params: {},
    data: {},
  });



